module.exports = "/* ========================================================================\r\n   Component: preloader\r\n ========================================================================== */\n/*@noflip*/\n.preloader {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  overflow: hidden;\n  background-color: #3F51B5;\n  /* Chrome 10+, Saf5.1+ */\n  /* FF3.6+ */\n  /* IE10 */\n  /* Opera 11.10+ */\n  background-image: linear-gradient(90deg, #3F51B5 10%, #3849a2 90%);\n  /* W3C */\n  z-index: 9999;\n  transition: opacity 0.65s; }\n/*@noflip*/\n.preloader-progress {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  width: 100px;\n  height: 30px;\n  margin: auto;\n  overflow: auto;\n  background-image: url(data:image/png;base64,);\n  background-size: 100px 30px; }\n/*@noflip*/\n.preloader-progress-bar {\n  position: absolute;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  min-width: 10px;\n  background-image: url(data:image/png;base64,);\n  background-size: 100px 30px; }\n.preloader-hidden {\n  display: none; }\n.preloader-hidden-add {\n  opacity: 1;\n  display: block; }\n.preloader-hidden-add .preloader-progress {\n    transition: -webkit-transform 0.4s ease;\n    transition: transform 0.4s ease;\n    transition: transform 0.4s ease, -webkit-transform 0.4s ease;\n    -webkit-transform: scale(0);\n    transform: scale(0); }\n.preloader-hidden-add-active {\n  opacity: 0; }\n"